import { createApp } from 'vue'
import App from './App.vue'

import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faHouse, faTelevision,faCubes,faContactBook, faShoppingCart, faDirections,faArrowUp, faGifts} from '@fortawesome/free-solid-svg-icons'

library.add(faHouse,faTelevision,faCubes,faContactBook,faShoppingCart,faDirections,faArrowUp,faGifts)

createApp(App)
    .component('font-awesome-icon', FontAwesomeIcon)
    .mount('#app')
