<template>
    <div>
        <button @click="scrollToTop"><font-awesome-icon icon="arrow-up"/> Volver arriba</button>
    </div>
</template>

<script>
export default {
    methods: {
        scrollToTop() {
            window.scrollTo({
            top: 0,
            behavior: "smooth" // Esto proporciona un desplazamiento suave
            });
        }
    }
};
</script>

