                price1:"20.000"},
                { name: "Netflix & Paramount", 
                image:require("@/assets/combos/netflix-paramount.png"), 
                price1:"19.000"},
                { name: "Disney + & HBO-MAX", 
                image: require("@/assets/combos/disney-hbo.png"), 
                price1: "$8.000",
                price2: "16.000"},
                { name: "Disney + & Start +", 
                image: require("@/assets/combos/disney-start.png"), 
                price1: "$10.000" },
                { name: "Disney + & Paramount", 
