<template>
  <div id="app">
    <div class="nav-bar"> 
      <!-- Botones de la barra de navegacion -->
        <button @click="scrollToSection('ofertasEspeciales')"><font-awesome-icon icon="gifts"/> Ofertas</button>
        <button @click="scrollToSection('plataformas')"><font-awesome-icon icon="tv" /> Plataformas</button>
        <button @click="scrollToSection('combos')"><font-awesome-icon icon="cubes" /> Combos</button>
        <button @click="scrollToSection('contact')"> <font-awesome-icon icon="address-book" /> Contacto</button>
    </div>
    <div id="inicio" class="section">
      <HelloWorld/>
    </div>
    <div>
      <MediosPago/>
    </div>
    <div id="ofertasEspeciales" class="setion">
      <OfertasEspeciales/>
    </div>
    <div id="plataformas" class="section">
      <PlataformsVue/>
    </div>
    <div id="combos" class="section">
      <CombosVue/>
    </div>
    <div id="contact" class="section">
      <ContacVue/>
    </div>
    <!-- Botón "Volver arriba" -->
    <ScrollTopButton v-show="hasScrolled" />
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import ScrollTopButton from './components/TopButton.vue'
import PlataformsVue from './components/PlataformsVue.vue'
import CombosVue from './components/CombosVue.vue'
import ContacVue from './components/ContacVue.vue'
import MediosPago from './components/MediosPago.vue'
import OfertasEspeciales from './components/OfertasEspeciales.vue'

export default {
  name: 'App',
  components: {
    HelloWorld,
    PlataformsVue,
    CombosVue,
    ScrollTopButton,
    ContacVue,
    MediosPago,
    OfertasEspeciales
  },
  data() {
    return {
      hasScrolled: false
    }
  },
  methods: {
    handleScroll() {
      // Método para manejar el evento de scroll
      this.hasScrolled = window.scrollY > 0
    },
    scrollToSection(sectionId) {
      // Método para hacer scroll hacia la sección correspondiente
      const sectionElement = document.getElementById(sectionId)
      if (sectionElement) {
        sectionElement.scrollIntoView({ behavior: 'smooth' })
      }
    }
  },
  mounted() {
    // Se registra el event listener para el evento de scroll
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeUnmount() {
    // Se remueve el event listener cuando el componente se desmonta
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

