<template>
    <!-- Contenedor principal del componente -->
    <div class="content">
        <!-- Lista de información de contacto -->
        <ul>
            <!-- Elemento de la lista para el teléfono -->
            <li><a :href="whatsappLink" target="_blank">
            <font-awesome-icon icon="address-book" /> Teléfono: {{ telefono }}</a>
            </li>
            <!-- Elemento de la lista para la dirección -->
            <li><font-awesome-icon icon="directions" /> Dirección: Cali, Colombia</li>
        </ul>
        <!-- Texto de derechos de autor -->
        <p>©{{ copyright }} StreamingYa | Todos los derechos reservados.</p>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // Datos del componente
            telefono: "3204152270",
            copyright: "",
            defaultWhatsAppMessage: "¡Hola! Estoy interesado en obtener más informacion. ¿Podrian ayudarme?"
        };
    },
    computed:{
        whatsappLink(){
            const phoneNumber = "+57"+this.telefono; 
            const encodedMessage = encodeURIComponent(this.defaultWhatsAppMessage);
            return `https://wa.me/${phoneNumber}?text=${encodedMessage}`; 
        } 
    },
    mounted() {
        // Llama al método para actualizar el año al montar el componente
        this.updateYear();
    },
    methods: {
// Método para actualizar el año de los derechos de autor
updateYear() {
            // Obtiene el año actual
            const currentDate = new Date();
            const currentYear = currentDate.getFullYear();
            // Actualiza el año en los derechos de autor
            this.copyright = currentYear; // Corrección aquí
            // Establece un intervalo para verificar si el año ha cambiado
            setInterval(() => {
                const newDate = new Date();
                const newYear = newDate.getFullYear();
                // Si el año ha cambiado, actualiza los derechos de autor
                if (newYear !== currentYear) {
                    this.copyright = newYear;
                }
            }, 60000); // Actualizar cada minuto    
        }
    }
};
</script>

