<template>
    <!-- Contenedor principal del componente -->
    <div class="container">
        <!-- Título del componente -->
        <h2>Plataformas</h2>
        <!-- Contenedor de las tarjetas -->
        <div class="container-card">
            <!-- Iteración sobre las plataformas para mostrar cada tarjeta -->
            <div class="card" v-for="(platform, index) in platforms" :key="index">
                <!-- Nombre de la plataforma -->
                <h3>{{ platform.name }}</h3>
                <!-- Imagen de la plataforma -->
                <img :src=platform.image :alt="platform.name">
                <!-- Precios de las suscripciones -->
                <button @click="toggleMenu(platform)"><font-awesome-icon icon="cart-shopping" /> Comprar</button>
                <div v-if="platform.showMenu" class="menu" v-click-outside="closeMenu">
                    <template v-if="platform.name === 'Spotify' || platform.name === 'Youtube'">
                        <button v-if="platform.price1" @click="selectSubscription(platform, '1 Mes')">1 Mes {{ platform.price1 }}</button>
                        <button v-if="platform.price2" @click="selectSubscription(platform, '2 Meses')">2 Meses {{ platform.price2 }}</button>
                        <button v-if="platform.price3" @click="selectSubscription(platform, '3 Meses')">3 Meses {{ platform.price3 }}</button>
                    </template>
                    <template v-else>
                        <button v-if="platform.price1 && platform.price2 && platform.price3" @click="selectSubscription(platform, '1 Pantalla')">1 Pantalla {{ platform.price1 }}</button>
                        <button v-if="platform.price1 && platform.price2 && platform.price3" @click="selectSubscription(platform, '2 Pantallas')">2 Pantallas {{ platform.price2 }}</button>
                        <button v-if="platform.price1 && platform.price2 && platform.price3" @click="selectSubscription(platform, 'Cuenta Completa')">Cuenta Completa {{ platform.price3 }}</button>
                        <button v-if="platform.price1 && platform.price2 && !platform.price3" @click="selectSubscription(platform, '1 Pantalla')">1 Pantalla {{ platform.price1 }}</button>
                        <button v-if="platform.price1 && platform.price2 && !platform.price3" @click="selectSubscription(platform, '2 Pantallas')">2 Pantallas {{ platform.price2 }}</button>
                        <button v-if="platform.price1 && !platform.price2 && !platform.price3" @click="selectSubscription(platform, '1 Pantalla')">1 Pantalla {{ platform.price1 }}</button>
                    </template>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import VueClickOutside from 'v-click-outside';

export default {
    directives: {
        ClickOutside: VueClickOutside.directive
    },
    data() {
        return {
            // Datos de las plataformas
            platforms: [
                { name: "Netflix", 
                image: require("@/assets/plataformas/1.png"), 
                price1: "$14.000"
            },
                { name: "HBO-MAX", 
                image: require("@/assets/plataformas/2.png"),
                price1: "$6.000", 
                price2: "$9.000", 
                price3: "$8.000" 
            },
                { name: "Disney +", 
                image: require("@/assets/plataformas/3.png"),
                price1: "$13.000"
            },
                { name: "Amazon Prime", 
                image: require("@/assets/plataformas/4.png"), 
                price1: "$8.000", 
                price2:"12.000" 
            },
                { name: "Start +", 
                image: require("@/assets/plataformas/5.png"), 
                price1: "$8.0000"
            },
                { name: "Plex", 
                image: require("@/assets/plataformas/6.png"), 
                price1: "$10.000", 
                price2: "$12.000", 
                price3: "$15.000" 
            },
                { name: "Spotify", 
                image: require("@/assets/plataformas/7.png"), 
                price1: "$12.000", 
                price2: "$16.000", 
                price3:"20.000"
            },
                { name: "Youtube", 
                image: require("@/assets/plataformas/9.png"), 
                price1: "10.000" 
            },
                { name: "Crunchyroll", 
                image: require("@/assets/plataformas/10.png"), 
                price1: "$9.000"
            },
                { name: "El Profenet", 
                image: require("@/assets/plataformas/elprofenet.png"), 
                price1:"14.000"
            },
                { name: "Paramount", 
                image: require("@/assets/plataformas/paramount.png"), 
                price1:"7.000", 
                price2:"12.000"
            },
                { name: "Vix", 
                image: require("@/assets/plataformas/vix.png"), 
                price1:"8.000", 
                price2:"15.000"
            }

            ],
            selectedSubscription: null
        };
    },
    methods: {        
        toggleMenu(platform) {
            this.platforms.forEach(p => {
                if (p !== platform) {
                     p.showMenu = false; // Cierra los menús de otras plataformas
                }
            });
            platform.showMenu = !platform.showMenu;
        },
        closeMenu() {
            this.platforms.forEach(p => p.showMenu = false); // Cierra todos los menús al hacer clic fuera de ellos
        },
        selectSubscription(platform, subscription) {
            this.selectedSubscription = subscription;
            this.sendToWhatsApp(platform, subscription);            
        },
        sendToWhatsApp(platform, subscription) {
            let price = "";
            // Determina el precio según la plataforma y la suscripción seleccionadas
            switch (subscription) {
                case '1 Pantalla':
                    price = platform.price1;
                    break;
                case '2 Pantallas':
                    price = platform.price2;
                    break;
                case 'Cuenta Completa':
                    price = platform.price3;
                    break;
                default:
                    if (platform.name === 'Spotify') {
                        // Si es Spotify, usa la duración seleccionada para obtener el precio
                        if (subscription === '1 Mes') {
                            price = platform.price1;
                            console.log(price)
                        } else if (subscription === '2 Meses') {
                            price = platform.price2;
                            console.log(price)
                        } else if (subscription === '3 Meses'){
                            console.log(price)
                            price = platform.price3;
                        }
                    } else if (platform.name === 'Youtube') {
                        // Si es Youtube, usa la duración seleccionada para obtener el precio
                        if (subscription === '1 Mes') {
                            price = platform.price1;
                        }
                    }
                    break;
                    
            }
            const message = `Quiero comprar ${platform.name}  - ${subscription} - ${price}.`;
            const phoneNumber = '+573204152270';
            const whatsappLink = `https://wa.me/${phoneNumber}?text=${encodeURIComponent(message)}`;
            window.open(whatsappLink, '_blank');
        },
    }
};
</script>

