<template>
    <!-- Contenedor principal del componente -->
    <div class="container">
        <!-- Título del componente -->
        <h2>Ofertas Especiales</h2>
        <!-- Contenedor de las tarjetas -->
        <div class="container-card">
            <!-- Iteración sobre las plataformas para mostrar cada tarjeta -->
            <div class="card" v-for="(platform, index) in platforms" :key="index">
                <!-- Nombre de la plataforma -->
                <h3>{{ platform.name }}</h3>
                <!-- Imagen de la plataforma -->
                <img :src=platform.image :alt="platform.name">
                <!-- Precios de las suscripciones -->
                <button @click="toggleMenu(platform)"><font-awesome-icon icon="cart-shopping" /> Comprar</button>
                <div v-if="platform.showMenu" class="menu" v-click-outside="closeMenu">
                    <button v-if="platform.price1 && platform.price2 && platform.price3" @click="selectSubscription(platform, 'cuenta Completa',platform.
                    duracion[0])">Cuenta completa {{platform.duracion[0]}} {{ platform.price1 }}</button>

                    <button v-if="platform.price1 && platform.price2 && platform.price3" @click="selectSubscription(platform, 'cuenta Completa',platform.
                    duracion[1] )">Cuenta completa {{platform.duracion[1]}} {{ platform.price2 }}</button>

                    <button v-if="platform.price1 && platform.price2 && platform.price3" @click="selectSubscription(platform, 'cuenta Completa', platform.duracion[2])">Cuenta completa {{platform.duracion[2]}} {{ platform.price3 }}</button>

                    <button v-if="platform.price1 && platform.price2 && !platform.price3" @click="selectSubscription(platform, '1 Pantalla',platform.
                    duracion[0])">1 Pantalla {{platform.duracion[0]}} {{ platform.price1 }}</button>

                    <button v-if="platform.price1 && platform.price2 && !platform.price3" @click="selectSubscription(platform, '2 Pantallas',platform.
                    duracion[1])">2 Pantallas {{platform.duracion[1]}} {{ platform.price2 }}</button>

                    <button v-if="platform.price1 && !platform.price2 && !platform.price3" @click="selectSubscription(platform, 'cuenta completa',platform.duracion[0])">Cuenta completa {{platform.duracion[0]}} {{ platform.price1 }}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import VueClickOutside from 'v-click-outside';

export default {
    directives: {
        ClickOutside: VueClickOutside.directive
    },
    data() {
        return {
            // Datos de las plataformas
            platforms: [
                { name: "Disney +", 
                image: require("@/assets/plataformas/3.png"), 
                price1: "$32.500", 
                duracion: ["3 meses"]
            },
                { name: "HBO-MAX", 
                image: require("@/assets/plataformas/2.png"), 
                price1: "$20.000", 
                duracion: ["3 meses"]
            },
                { name: "Netflix", 
                image: require("@/assets/plataformas/1.png"), 
                price1:"$25.000", 
                price2:"$35.000", 
                duracion:["2 meses","3 meses"]
            },
                { name: "Netflix Completa", 
                image: require("@/assets/plataformas/1.png"), 
                price1:"$45.000", 
                price2:"$87.500", 
                price3:"$120.000", 
                duracion:["1 mes","2 meses","3 meses"]
            },
            ],
            selectedSubscription: null
        };
    },
    methods: {
        toggleMenu(platform) {
            this.platforms.forEach(p => {
                if (p !== platform) {
                     p.showMenu = false; // Cierra los menús de otras plataformas
                }
            });
            platform.showMenu = !platform.showMenu;
        },
        closeMenu() {
            this.platforms.forEach(p => p.showMenu = false); // Cierra todos los menús al hacer clic fuera de ellos
        },
        selectSubscription(platform, subscription, duracion) {
            this.selectedSubscription = subscription;
            this.sendToWhatsApp(platform, subscription, duracion);
        },
        sendToWhatsApp(platform, subscription, duracion) {
            let price = "";
            switch (subscription) {
                case '1 pantalla':
                    price = platform.price2;
                    break;
                case '2 pantallas':
                    price = platform.price3;
                    break;
                case 'cuenta completa':
                    price = platform.price1;
                    break;
                default:
                    if (platform.name === 'Netflix') {
                        if (duracion === '2 meses') {
                            price = platform.price1;
                        } else if (duracion === '3 meses') {
                            price = platform.price2;
                        }
                    } else if (platform.name === 'Netflix Completa') {
                        if (duracion === '1 mes') {
                            price = platform.price1;
                        } else if (duracion === '2 meses') {
                            price = platform.price2;
                        } else if (duracion === '3 meses') {
                            price = platform.price3;
                        }
                    }
                    break;
            }
        const message = `Quiero comprar ${platform.name} - ${duracion} - ${subscription} - ${price}.`;
        const phoneNumber = '+573204152270'; 
        const whatsappLink = `https://wa.me/${phoneNumber}?text=${encodeURIComponent(message)}`;
        window.open(whatsappLink, '_blank');
    },
    
    }
};
</script>

